const Directory = ({ selectMainMenuSections }) => {
    return (
      <div className='directory-menu'>
        {selectMainMenuSections.map(({ id, ...otherSectionProps }) => {
          return (
            <MenuItem key={id} {...otherSectionProps} />
          )
        })}
      </div>
    )
}

